<template>
  <div style="width: 100%; height: 100%">
    <div style="width: 96%; height: 100%; background-color: #fff">
      <div
        style="
          width: 100%;
          height: 4em;
          margin-top: 1%;
          text-align: left;
          padding-top: 1%;
        "
      >
        <el-select
          v-model="filter.relayType"
          placeholder="中继类型"
          @change="filterData"
          style="margin-left=1%"
        >
          <el-option label="奥科" value="奥科"></el-option>
          <el-option label="IMS" value="IMS"></el-option>
          <el-option label="网经" value="网经"></el-option>
          <el-option label="NGN" value="NGN"></el-option>
        </el-select>
        <el-select
          v-model="filter.callForward"
          placeholder="是否呼转"
          @change="filterData"
          style="margin-left=1%"
        >
          <el-option label="全部" value="全部"></el-option>
          <el-option label="部分" value="部分"></el-option>
          <el-option label="全部不" value="全部不"></el-option>
        </el-select>
        <el-select
          v-model="filter.billing"
          placeholder="是否计费"
          @change="filterData"
          style="margin-left=1%"
        >
          <el-option label="全部" value="全部"></el-option>
          <el-option label="部分" value="部分"></el-option>
          <el-option label="全部不" value="全部不"></el-option>
        </el-select>
        <el-input placeholder="请输入关键字" style="width: 25%"></el-input>
      </div>

      <el-table
        :data="filteredData"
        :header-cell-style="{ textAlign: 'center' }"
      >
        <el-table-column prop="province" label="省/市"></el-table-column>
        <el-table-column prop="relayType" label="中继类型"></el-table-column>
        <el-table-column prop="callForward" label="是否呼转"></el-table-column>
        <el-table-column
          prop="callForwardRange"
          label="呼转范围"
        ></el-table-column>
        <el-table-column prop="billing" label="是否计费"></el-table-column>
        <el-table-column
          prop="billingRange"
          label="不计费范围"
        ></el-table-column>
        <el-table-column label="操作">
          <el-button
            @click="openEditDialog(scope.row)"
            type="text"
            icon="el-icon-edit"
          ></el-button>
          <el-button
            @click="delOpen(1, scope.row)"
            type="text"
            icon="el-icon-delete"
          ></el-button>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import data from "../../assets/js/systemData.js";

export default {
  data() {
    return {
      filter: {
        relayType: "",
        callForward: "",
        billing: "",
      },
      tableData: data,
    };
  },
  computed: {
    filteredData() {
      let filtered = this.tableData.data;
      if (this.filter.relayType) {
        filtered = filtered.filter(
          (item) => item.relayType === this.filter.relayType
        );
      }
      if (this.filter.callForward) {
        filtered = filtered.filter(
          (item) => item.callForward === this.filter.callForward
        );
      }
      if (this.filter.billing) {
        filtered = filtered.filter(
          (item) => item.billing === this.filter.billing
        );
      }
      return filtered;
    },
  },
  methods: {
    filterData() {
      // 这里你可以添加一些逻辑，根据选择的条件重新过滤数据
    },
    handleEdit() {
      // 编辑操作
    },
    handleDelete() {
      // 删除操作
    },
  },
};
</script>

<style>
.el-table__body-wrapper .cell {
  text-align: center;
}

.el-select {
  width: 8em !important;
  margin-left: 1% !important;
}

.el-input--suffix .el-input__inner {
  border: 1px solid rgb(233, 233, 233) !important;
}

body {
  overflow: hidden;
}
</style>
